import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import './App.css';
import { useTranscription } from './hooks/useTranscription';
import { useSummarization } from './hooks/useSummarization';

function App() {
  const {
    isTranscribing,
    confirmedText,
    unconfirmedTextPreview,
    modelReady: transcriptionModelReady, // Renamed for clarity in App.js
    modelLoadingProgress: transcriptionModelLoadingProgress, // New state from hook
    // statusMessage: transcriptionStatusMessage, // Renamed to avoid clash
    isMicMuted,
    isPaused, // New state from hook
    isProcessingAudioRef: isTranscriptionProcessingAudioRef, // Exposed from hook
    handleStartTranscription,
    handleStopTranscription: stopTranscriptionHook, // Renamed to avoid clash with wrapper
    handlePauseTranscription, // New handler from hook
    handleResumeTranscription, // New handler from hook
    handleToggleMic,
  } = useTranscription();

  const {
    summaryText,
    isSummarizing,
    summarizationModelReady, // Exposed from hook
    summarizationModelLoadingProgress, // New state from hook
    // summarizationStatus,
    summarizationState,
    isLoadingSummarizationModelRef, // Exposed from hook
    handleStartSummarization: startSummarizationHook, // Renamed
    interruptSummarization,
  } = useSummarization();

  const [meetingTitle, setMeetingTitle] = useState("Your Meeting Title"); // Editable title
  const [webGpuAvailable, setWebGpuAvailable] = useState(true); // Assume available until checked

  useEffect(() => {
    const checkWebGPU = async () => {
      if (navigator.gpu) {
        try {
          const adapter = await navigator.gpu.requestAdapter();
          if (!adapter) {
            setWebGpuAvailable(false);
            console.warn("WebGPU not available: No adapter found.");
          } else {
            setWebGpuAvailable(true);
            console.log("WebGPU is available.");
          }
        } catch (error) {
          setWebGpuAvailable(false);
          console.error("Error requesting WebGPU adapter:", error);
        }
      } else {
        setWebGpuAvailable(false);
        console.warn("WebGPU not available: navigator.gpu is not defined.");
      }
    };
    checkWebGPU();
  }, []);

  const handleMeetingTitleChange = (event) => {
    setMeetingTitle(event.target.value);
  };

  // Wrapper to also interrupt summarization when transcription stops
  const handleStopTranscription = () => {
    stopTranscriptionHook();
    if (isSummarizing) {
      interruptSummarization();
    }
  };

  // Wrapper to pass confirmedText and meetingTitle to summarization, now called by "Generate Note"
  const handleGenerateNote = () => {
    startSummarizationHook(confirmedText, meetingTitle); // Pass title
  };

  // Placeholder for new actions
  const handleCopyTranscript = () => {
    navigator.clipboard.writeText(confirmedText)
      .then(() => console.log("Transcript copied to clipboard"))
      .catch(err => console.error("Failed to copy transcript: ", err));
  };

  const handleCopySummaryAsMarkdown = () => {
    navigator.clipboard.writeText(summaryText)
      .then(() => console.log("Summary copied to clipboard as Markdown"))
      .catch(err => console.error("Failed to copy summary: ", err));
  };

  const handleRegenerateSummary = () => {
    // This would likely re-trigger summarization, possibly with different params or context
    console.log("Regenerate summary action triggered");
    if (confirmedText.trim()) {
      startSummarizationHook(confirmedText, meetingTitle); // Pass title
    }
  };
  
  // const meetingTitle = "Product Announcements and Event Planning Meeting"; // Placeholder -- replaced by state

  return (
    <div className="App-container">
      {!webGpuAvailable && (
        <div className="webgpu-unavailable-banner">
          <p>⚠️ WebGPU is not available or not supported by your browser. This application may not function correctly. Please use a browser that supports WebGPU (like Chrome or Edge) and ensure hardware acceleration is enabled.</p>
        </div>
      )}
      {/* 
      <div className="sidebar">
        <div className="sidebar-icon">🗓️</div> 
        <div className="sidebar-icon">📄</div>
        <div className="sidebar-icon sidebar-arrow">❯</div>
      </div> 
      */}

      <div className="main-content-wrapper">
        {/* 
        <div className="top-bar">
          <div className="brand-name">meetily</div>
          <div className="top-bar-actions">
            <span className="top-bar-icon">↩️</span> 
            <span className="top-bar-icon">↪️</span>
            <span className="top-bar-icon">➕</span>
          </div>
        </div> 
        */}

        <div className="content-area">
          {/* Left Panel: Transcription */}
          <div className="left-panel">
            <div className="left-panel-header">
              <input 
                type="text" 
                value={meetingTitle} 
                onChange={handleMeetingTitleChange} 
                className="meeting-title-input"
                placeholder="Enter meeting title"
              />
              {/* Controls are now grouped into a toolbar below the input */}
              <div className="panel-controls-toolbar"> 
                <div className="header-audio-controls">
                  <button onClick={handleToggleMic} disabled={!isTranscribing} className="icon-button" title={isMicMuted ? "Unmute Microphone" : "Mute Microphone"}>
                    {isMicMuted ? '🔇 Unmute Mic' : '🎤 Mute Mic'}
                  </button>
                  {!isTranscribing ? (
                    <button 
                      onClick={handleStartTranscription} 
                      disabled={isTranscriptionProcessingAudioRef.current} 
                      className="play-button record-button" 
                      title="Start Recording"
                    >
                      🔴 Start Recording
                    </button>
                  ) : (
                    <button 
                      onClick={handleStopTranscription} 
                      className="play-button stop-button" 
                      title="Stop Recording"
                    >
                      🟥 Stop Recording
                    </button>
                  )}
                </div>
                <div className="left-panel-actions">
                  <button onClick={handleCopyTranscript} disabled={!confirmedText.trim()}>
                    📋 Copy Transcript
                  </button>
                  <button 
                    onClick={handleGenerateNote} 
                    disabled={!confirmedText.trim() || isSummarizing || isLoadingSummarizationModelRef.current || isTranscribing}
                  >
                    ✨ Generate Note
                  </button>
                </div>
              </div>
              {!transcriptionModelReady && transcriptionModelLoadingProgress && (
                <p className="loading-status">{transcriptionModelLoadingProgress}</p>
              )}
            </div>
            {(isTranscribing || confirmedText.trim()) && (
            <div className="transcription-display-area">
              {/* This area will show the scrolling transcript */}
              <span className="confirmed-text">{confirmedText}</span>
              <span className="unconfirmed-text">{unconfirmedTextPreview}</span>
            </div>
            )}
            {!isTranscribing && !confirmedText.trim() && (
              <div className="transcription-display-area placeholder-mode">
                 <p className="placeholder-text">Transcription will appear here once recording starts...</p>
              </div>
            )}
            <div className="audio-controls">
              {/* Timer and progress bar removed */}
              
              {/* Pause/Resume and separate Stop button are removed in favor of a single Record/Stop button */}
            </div>
          </div>

          {/* Right Panel: AI Enhanced Summary */}
          <div className="right-panel">
            <div className="right-panel-header">
              <h3>✨ AI Enhanced Summary</h3>
              <div className="right-panel-actions">
                <button onClick={handleCopySummaryAsMarkdown} disabled={!summaryText.trim() || isSummarizing}>
                  📋 Copy as Markdown
                </button>
              </div>
            </div>
            <div className="summary-display-area">
              {!summarizationModelReady && summarizationModelLoadingProgress && (
                <p className="loading-status">{summarizationModelLoadingProgress}</p>
              )}
              {isSummarizing && <p>Generating summary... ({summarizationState})</p>}
              {!isSummarizing && !summaryText.trim() && summarizationModelReady && <p className="placeholder-text">Summary notes will appear here once generated.</p>}
              {summaryText.trim() && !isSummarizing && (
                // Placeholder for structured summary. Current summaryText is a single block.
                // This will need significant changes to how summary is structured and displayed.
                <div>
                  <div className="summary-section">
                    <h4>Section Summary</h4>
                    <ReactMarkdown>{summaryText}</ReactMarkdown> {/* Displaying full summary as placeholder */}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <footer className="App-footer-condensed">
        <p>Ensure "Share tab audio" is enabled for transcription.</p>
      </footer>
    </div>
  );
}

export default App;
